@import "../vars/vars.scss";
@import "../styleHeader/styleHeader.scss";
@import "../styleFooter/styleFooter.scss";
//--------------------------------------------------------------------------------------------//
body {
    font-family: $main-font;
    @include universal;
}

a {
    @include link;
}

ol,
ul,
li {
    @include list-style;
}

img {
    vertical-align: top;
}

.wrapper {
    @include f-column;
    min-height: 100%;
    overflow: hidden;
    background-color: rgba(251, 251, 251, 1);
    .floating-button {
        position: fixed;
        z-index: 10;
        right: 30px;
        bottom: 30px;
        background: #f088008d;
        border-radius: 50%;
        width: 70px;
        height: 70px;
        img {
            position: absolute;
            max-width: 40px;
            top: 15px;
            left: 15px;
        }
    }
    .floating-button:hover {
        background: $main-orange-color;
        box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.5);
    }
}

.container {
    max-width: 1230px;
    margin: 0px auto;
    padding: 0px 15px;
}

//---------------------------main__block------------------------
.image__block {
    display: flex;
    height: 740px;
    background: url("../../../images/imagesMain/background.png");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}

//-------------------------name-------------------------
.name {
    display: flex;
    flex-direction: row-reverse;
    @include font-header;
    text-align: center;
    position: relative;
    z-index: 2;
    &__body {
        margin-top: -500px;
        @include f-column;
        flex: 0 1 50%;
        min-height: 1200px;
        background-color: $main-orange-color;
    }
    &__top {
        flex: 1 1 auto;
    }
    &__top-title {
        font-weight: 800;
        font-size: 50px;
        text-transform: uppercase;
        padding: 30px 0px;
    }
    &__top-text {
        padding: 10px 20px;
        color: #425b69;
        h5 {
            text-transform: uppercase;
            font-size: 20px;
            color: #425b69;
            color: white;
        }
        .name__adv {
            display: flex;
            flex-wrap: wrap;
            row-gap: 10px;
            padding: 20px 20px 20px 20px;
            color: #425b69;
            text-transform: uppercase;
            font-size: 15px;
            h6 {
                color: #f3f4fa;
            }
            img {
                width: 15%;
            }
        }
        .name__adv-block {
            flex: 0 1 50%;
        }
    }
    &__top-button {
        @include f-center;
        flex-wrap: wrap;
        margin: 0px;
        background-color: $main-orange-color;
    }
    &__image {
        display: flex;
        margin: 0 auto;
        max-width: 462px;
        margin-bottom: 40px;
        img {
            width: 100%;
        }
    }
}

.button {
    border-radius: 38px;
    margin: 10px 0px;
    @include f-center;
    align-items: center;
    padding: 5px 20px;
    min-height: 48px;
    letter-spacing: 0.2px;
    font-weight: 400;
    font-size: 16px;
    line-height: 105%;
    text-transform: uppercase;
    text-align: center;
    color: $main-color;
    background-color: rgba(87, 162, 83, 0.979);
    &:hover {
        background: $main-color;
        color: rgba(98, 99, 55, 1);
        text-decoration: none;
        transform: translateY(-5px);
        box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.2);
    }
}

//--------------------------------about--------------------------
.about {
    @include font-header;
    margin-top: -700px;
    &__container {
        margin-bottom: 30px;
    }
    &__body {
        display: flex;
        min-height: 900px;
    }
    &__name {
        p {
            margin-right: 15px;
        }
        display: flex;
        flex-wrap: nowrap;
        font-weight: 800;
        font-size: 130px;
        line-height: 105%;
        letter-spacing: -0.04em;
        text-transform: uppercase;
        color: #425b69;
        padding: 30px 20px;
        text-align: center;
        position: relative;
        z-index: 3;
    }
    &__left {
        @include f-column;
        flex: 0 1 50%;
    }
    &__left-content {
        flex: 1 1 auto;
    }
    &__title {
        text-align: center;
        font-weight: 500;
        font-size: 30px;
        line-height: 105%;
        text-transform: uppercase;
        padding: 10px 0px;
        color: #f08700;
    }
    &__text {
        padding: 20px;
        color: $paragraph-color;
        text-align: justify;
    }
    &__image {
        display: flex;
        margin: 0 auto;
        max-width: 462px;
        padding: 0px;
        img {
            width: 100%;
        }
    }
    &__right {
        @include f-column;
        flex: 0 1 50%;
    }
    &__right-content {
        transform: translateY(500px);
        margin-bottom: 20px;
        padding-bottom: 20px;
    }
}

//--------------------------------------------------------pansion----------------------------------------
.pansion {
    @include font-header;
    padding-top: 10%;
    color: $main-orange-color;
    h2 {
        @include h2;
        text-align: start;
    }
    &__container {
        display: flex;
        flex-direction: column;
        margin: 30px 0px;
    }
    &__gallery {
        .carousel {
            margin-top: 30px;
        }
        .carousel-inner {
            height: 400px;
        }
        .carousel-caption {
            color: $paragraph-color;
            font-family: $main-font;
            top: 50%;
            background-repeat: no-repeat;
            background-position: center center;
            height: 400px;
        }
    }
}

//---------------------------------------------courses------------------------------------
.courses {
    &__title {
        h2 {
            @include h2;
            display: flex;
            justify-content: center;
            padding: 15px;
            flex-wrap: wrap;
            color: #f08700;
        }
    }
    &__column {
        display: flex;
        box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
        height: 340px;
        padding: 30px;
        margin-bottom: 2%;
        &:hover {
            box-shadow: 5px 5px 5px #425b69;
            transition: all 0.3s ease 0s;
            border: solid 4px $main-orange-color;
        }
    }
    &__img {
        max-width: 100%;
    }
    &__image {
        width: 100%;
    }
    &__description {
        display: flex;
        flex-direction: column;
        padding-left: 40px;
    }
    &__title {
        h3,
        h4 {
            color: #454545;
        }
    }
    &__subtitle {
        color: #454545;
    }
    &__time {
        p {
            color: #454545;
            margin-bottom: 0;
        }
    }
    &__time-flex {
        display: flex;
        align-items: baseline;
        gap: 10px;
    }
    &__place {
        display: flex;
        align-items: baseline;
        gap: 10px;
    }
    &__button {
        border-radius: 25px;
        background-color: $main-orange-color;
        color: #000;
    }
}

//---------------------------------------advantages----------------------------------------------//
.advantages {
    padding-top: 30px;
    h2 {
        @include h2;
        @include f-center;
        padding-top: 30px;
    }
    &__subtitle {
        color: $paragraph-color;
        font-size: 20px;
        @include f-center;
        margin-bottom: 4%;
    }
    &__wrapper {
        width: 90%;
        margin: 2% auto;
    }
    &__block {
        display: grid;
        grid-template-columns: repeat(3, 3fr);
        grid-template-rows: repeat(2, 3fr);
        grid-gap: 3%;
    }
    &__card {
        width: 100%;
        @include f-column;
        justify-content: center;
        align-items: center;
        border-radius: 5px;
        transition: all 0.25s ease-in-out;
        &:hover {
            background: $color-hover;
            color: rgb(0, 0, 0);
            border-radius: 20px;
            box-shadow: -1px 20px 10px 0px rgba(0, 0, 0, 0.212);
        }
        img {
            width: 30%;
        }
        h4 {
            margin-top: 5%;
            color: #425b69;
            font-family: sans-serif;
            font-weight: 800;
            font-size: 20px;
        }
        h5 {
            padding: 0 10px;
            color: gray;
            font-family: sans-serif;
            text-align: justify;
            font-size: 16px;
        }
    }
}

//---------------------------pansion------------------------
.pansion__gallery {
    .carousel-caption {
        background-color: #ffffff7d;
        color: #000000;
        border-radius: 10px;
        top: 63%;
    }
}

//---------------------------gallery------------------------
.gallery {
    @include f-center;
    @include f-column;
    align-items: center;
    h2 {
        margin-top: 5%;
        @include h2;
    }
    &__flags {
        @include f-center;
        gap: 20px;
        img {
            max-width: 5%;
        }
    }
    &__text {
        color: $paragraph-color;
        .bold {
            font-weight: bold;
        }
    }
    &__container {
        columns: 5 200px;
        column-gap: 1.5rem;
        width: 90%;
        margin: 0 auto;
        div {
            width: 150px;
            margin: 0 1.5rem 1.5rem 0;
            display: inline-block;
            width: 100%;
            margin-top: 10%;
            padding: 5px;
            border: solid 2px white;
            box-shadow: $box-shadow;
            border-radius: 5px;
            transition: all 0.25s ease-in-out;
            &:hover img {
                filter: grayscale(0);
            }
            &:hover {
                border: solid 4px $main-orange-color;
                box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.5);
            }
            img {
                width: 100%;
                filter: grayscale(100%);
                border-radius: 5px;
                transition: all 0.25s ease-in-out;
            }
            p {
                margin: 5px 0;
                padding: 0;
                text-align: center;
                font-style: italic;
                color: $paragraph-color;
                &:last-child {
                    color: $main-orange-color;
                }
            }
        }
    }
}

//---------------------------team------------------------
.team {
    &__container {
        width: 100%;
        margin: auto;
        padding: 0 15px;
    }
    &__title {
        flex: 0 0 100%;
        max-width: 100%;
        margin-bottom: 40px 0 80px 0;
        text-align: center;
        h2 {
            @include h2;
            display: inline-block;
            margin: 0 0 20px;
            position: relative;
            &::before {
                @include title-before;
                background-color: #425b69;
            }
        }
    }
    p {
        padding: 0;
        margin: 0 0 15px 0;
        color: $paragraph-color;
        font-weight: 300;
    }
    img {
        width: 100%;
        height: auto;
    }
    &__row {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        margin-right: -15px;
        margin-left: -15px;
    }
    &__column {
        position: relative;
        width: 100%;
        padding: 0 15px;
        flex: 0 0 100%;
        max-width: 100%;
        &:hover {
            background: $color-hover;
            color: rgb(0, 0, 0);
            border-radius: 20px;
            box-shadow: -1px 20px 10px 0px rgba(0, 0, 0, 0.212);
        }
        .team__card {
            margin-bottom: 30px;
            .team__img {
                position: relative;
                font-size: 0;
                text-align: center;
                img {
                    width: 100%;
                    height: auto;
                    border-radius: 100%;
                    border: 20px solid #f3f4fa;
                }
            }
            .team__content {
                padding: 80px 20px 20px 20px;
                margin-top: -80px;
                text-align: center;
                background: #f3f4fa;
                border-radius: 10px;
                h3 {
                    font-size: 25px;
                    font-weight: 400;
                    letter-spacing: 2px;
                    color: $paragraph-color;
                }
                h4 {
                    font-size: 16px;
                    font-weight: 300;
                }
                h5 {
                    font-size: 14px;
                    font-weight: 300;
                    letter-spacing: 1px;
                    font-style: italic;
                    margin-bottom: 0;
                }
                p {
                    font-size: 16px;
                    font-weight: 400;
                    line-height: 22px;
                    font-style: italic;
                }
            }
        }
    }
}

//---------------------------------------review----------------------------------------------//
.review {
    &__title {
        @include h2;
        padding: 5% 1% 1% 0;
        text-align: center;
    }
    &__text {
        color: $paragraph-color;
        font-size: 20px;
        @include f-center;
        margin-bottom: 2%;
        padding-left: 11%;
        padding-right: 3%;
        text-align: left;
    }
    .carousel {
        margin-top: 30px;
        &-inner {
            height: 400px;
        }
        &-caption {
            color: $paragraph-color;
            font-family: $main-font;
            top: 50%;
            background-repeat: no-repeat;
            background-position: center center;
            height: 400px;
        }
        .carousel-control-prev-icon {
            background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%23000' viewBox='0 0 8 8'%3E%3Cpath d='M5.25 0l-4 4 4 4 1.5-1.5-2.5-2.5 2.5-2.5-1.5-1.5z'/%3E%3C/svg%3E") !important;
        }
        .carousel-control-next-icon {
            background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%23000' viewBox='0 0 8 8'%3E%3Cpath d='M2.75 0l-1.5 1.5 2.5 2.5-2.5 2.5 1.5 1.5 4-4-4-4z'/%3E%3C/svg%3E") !important;
        }
    }
    .carousel-caption-first {
        background-image: url("../../../images/imagesMain/imagesReview/student-first1.png");
    }
    .carousel-caption-second {
        background-image: url("../../../images/imagesMain/imagesReview/student-second1.jpg");
    }
    .carousel-caption-third {
        background-image: url("../../../images/imagesMain/imagesReview/student-third1.png");
    }
    .carousel-caption-fourth {
        background-image: url("../../../images/imagesMain/imagesReview/student-fourth1.png");
    }
}

//-----------------------------------------price---------------------------------------------//
.price {
    @include f-column;
    h2 {
        @include h2;
        padding-top: 5%;
        color: #425b69;
    }
    &__info {
        @include regular-paragraphs;
        @include f-space-between;
        flex-direction: row;
        padding: 2% 5% 10% 5%;
        ol {
            list-style: none;
            padding-left: 0rem;
        }
        ol > li {
            padding-left: 20px;
            position: relative;
            margin: 5px 5px 5px 8px;
            color: $paragraph-color;
        }
        ol > li:before {
            content: "✔";
            position: absolute;
            left: -10px;
            color: rgb(5, 116, 2);
            font-weight: bold;
            font-size: 20px;
        }
        .no:before {
            content: "\274c";
            position: absolute;
            font-size: 14px;
        }
        &-bag {
            text-align: center;
            width: 100%;
            box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.5);
            border-radius: 10px;
            &-description {
                &-details {
                    text-align: start;
                    padding-left: 10px;
                    color: $paragraph-color;
                }
                &-title {
                    @include h2;
                    font-size: 20px;
                    font-weight: bold;
                    color: $paragraph-color;
                }
                &-rate {
                    @include regular-paragraphs;
                    font-size: 25px;
                    font-weight: bold;
                    color: $main-orange-color;
                    padding-bottom: 5px;
                }
            }
            &-description:hover {
                background: $color-hover;
                color: rgb(0, 0, 0);
                border-radius: 10px;
                box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.5);
                border: solid 4px $main-orange-color;
            }
        }
        .bag-light,
        .bag-standard {
            margin-right: 5%;
        }
        .btn {
            margin-bottom: 10px;
            cursor: pointer;
            @include main-button;
            background-color: $main-orange-color;
            width: 10vw;
            font-size: 12px;
            letter-spacing: 0.1em;
            border: none;
            &:hover {
                background: rgba(128, 128, 128, 0.719);
                box-shadow: 0px 5px 5px rgba(255, 0, 0, 0.212);
            }
        }
    }
}

//-----------------------------------------request---------------------------------------------//
.request {
    display: flex;
    justify-content: flex-end;
    background-image: url("../../../images/imagesMain/imgRequest/bg.png");
    background-color: rgb(251, 251, 251);
    display: flex;
    min-height: 430px;
    &__text {
        display: flex;
        flex-direction: column;
        justify-content: center;
        color: $main-color;
        padding: 30px 15% 0 0;
        h2 {
            color: $main-color;
            text-align: center;
            &:after {
                content: "";
                margin: 20px 0;
                display: block;
                height: 2px;
                background: white;
                width: 100%;
            }
        }
        p {
            margin-bottom: 20px;
            @include regular-paragraphs;
            color: $main-color;
            text-align: center;
        }
        .btn {
            display: flex;
            justify-content: center;
            position: relative;
            text-transform: uppercase;
            text-decoration: none;
            padding: 15px 40px;
            display: inline-block;
            border-radius: 100px;
            .btn:hover {
                background-color: #ffffff;
                border: none;
                transform: translateY(-3px);
                box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
            }
        }
    }
    #popup {
        position: fixed;
        width: 100%;
        height: 100vh;
        top: 0;
        left: 0;
        opacity: 0;
        visibility: hidden;
    }
    #popup:target {
        opacity: 1;
        visibility: visible;
    }
    .consult {
        background-color: rgba(0, 0, 0, 0.7);
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 15;
        .consult__content {
            position: relative;
            max-width: 400px;
            min-height: 500px;
            background-image: url("../../../images/imagesMain/imgRequest/2_2.jpeg");
            color: black;
            border-radius: 20px;
        }
        form {
            padding: 24px 0 0 43px;
            .consult__text {
                h2 {
                    color: #425b69;
                    font-size: 20px;
                    font-weight: 800;
                    text-transform: uppercase;
                    margin-bottom: 15px;
                    letter-spacing: 2px;
                }
                p {
                    font-size: 15px;
                    line-height: 20px;
                    margin-bottom: 25px;
                }
            }
            .consult__form {
                margin-bottom: 25px;
                .form__reg:focus::placeholder {
                    opacity: 0;
                }
                .form__reg {
                    background: transparent;
                    border: none;
                }
                .form__reg::placeholder {
                    color: #425b69;
                }
                .consult__name:after,
                .consult__number:after {
                    content: "";
                    margin: 3% 0;
                    display: block;
                    height: 1px;
                    max-width: 300px;
                    background: rgba(0, 0, 50, 0.4);
                }
            }
        }
        input[type="text"]:focus,
        input[type="number"]:focus {
            outline: none;
        }
        .btn {
            display: inline-block;
            width: 150px;
            font-size: 15px;
            border-radius: 100px;
            .btn:hover {
                transform: translateY(-3px);
                box-shadow: 0 10px 15px rgba(0, 0, 0, 0.2);
            }
            .btn-orange {
                background-color: $main-orange-color;
                color: rgb(255, 255, 255);
            }
        }
        .close-popup {
            position: absolute;
            right: -10px;
            top: -10px;
            width: 40px;
            height: 40px;
            background-color: $main-orange-color;
            color: #e2e8f0;
            text-decoration: none;
            text-align: center;
            line-height: 40px;
            border-radius: 50%;
        }
        .close-popup:hover {
            transform: translateY(-3px);
            box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
        }
    }
    .red {
        color: red;
    }
}

//--------------------------------------------questions----------------------------------
.questions {
    &__wrapper {
        font-family: $main-font;
        max-width: 1200px;
        margin: 0 auto;
        h2 {
            @include h2;
            color: #425b69;
            letter-spacing: 3px;
            padding-top: 60px;
        }
        .questions__block {
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 20px;
            padding-top: 30px;
            padding-bottom: 156px;
            .questions__item {
                display: block;
                background: rgb(231 133 22 / 53%);
                width: 1000px;
                box-shadow:
                    0 10px 15px -5px rgba(0, 0, 0, 0.1),
                    0 10px 10px -5px rgba(0, 0, 0, 0.04);
                border-radius: 8px;
                overflow: hidden;
            }
            summary::-webkit-details-marker {
                display: none;
            }
            summary::-moz-list-bullet {
                list-style-type: none;
            }
            summary::marker {
                display: none;
            }
            summary {
                display: block;
                padding: 20px;
                line-height: 25px;
                border-bottom: 1px solid #e2e8f0;
                font-size: 20px;
                cursor: pointer;
                position: relative;
                color: #425b69;
            }
            summary:before {
                top: 24px;
                right: 12px;
                color: transparent;
                background: url("../../../images/imagesHeader/free-icon-font-angle-down-3916886.svg") no-repeat 50% 50% /
                    1em 1em;
                width: 1em;
                height: 1em;
                content: "";
                position: absolute;
                transition: transform 0.5s;
            }
            details[open] > summary:before {
                transform: scale(1, -1);
            }
            summary ~ * {
                padding: 20px 20px 0 20px;
            }
            details[open] summary ~ * {
                color: #425b69;
            }
            summary:hover {
                background-color: #faa54ff8;
                transition: 0.5s;
            }
        }
    }
}

//-------------------------------------адаптив-main-name-about-----------------------------------------
@media (max-width: 1200px) {
    .about {
        &__name {
            font-size: 110px;
        }
        &__right-content {
            transform: translateY(520px);
            margin-bottom: 20px;
        }
    }
    .pansion {
        &__gallery {
            .carousel-caption {
                top: 64%;
                p {
                    font-size: 15px;
                    line-height: 22px;
                }
            }
        }
    }
    @media (max-width: 1023px) {
        .image__block {
            height: 500px;
            background: url("../../../images/imagesMain/background2.png");
            background-position: 0px 0px;
        }
        .name {
            &__body {
                margin-top: -420px;
                min-height: 1000px;
            }
            &__top-title {
                font-weight: 800;
                font-size: 45px;
            }
            &__top-text {
                padding: 5px 20px;
                h5 {
                    font-size: 18px;
                }
            }
            &__image {
                max-width: 410px;
            }
